@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: 'Kumbh Sans';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('../fonts/kumbh-sans-v11-latin-regular.woff2') format('woff2'),
      url('../fonts/kumbh-sans-v11-latin-regular.woff') format('woff');
  }
  @font-face {
    font-family: 'Kumbh Sans';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url('../fonts/kumbh-sans-v11-latin-700.woff2') format('woff2'),
      url('../fonts/kumbh-sans-v11-latin-700.woff') format('woff');
  }
  @font-face {
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('../fonts/roboto-slab-v23-latin-regular.woff2') format('woff2'),
      url('../fonts/roboto-slab-v23-latin-regular.woff') format('woff');
  }

  @font-face {
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url('../fonts/roboto-slab-v23-latin-700.woff2') format('woff2'),
      url('../fonts/roboto-slab-v23-latin-700.woff') format('woff');
  }
  @font-face {
    font-family: 'Space Mono';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('../fonts/space-mono-v11-latin-regular.woff2') format('woff2'),
      url('../fonts/space-mono-v11-latin-regular.woff') format('woff');
  }

  @font-face {
    font-family: 'Space Mono';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url('../fonts/space-mono-v11-latin-700.woff2') format('woff2'),
      url('../fonts/space-mono-v11-latin-700.woff') format('woff');
  }

  h1 {
    @apply text-xl;
  }

  h2 {
    @apply text-lg;
  }

  h3 {
    @apply text-base;
  }

  h4 {
    @apply text-xs;
  }
  
}

@layer components{
  .buttons-item{
    @apply rounded-[26px] px-[25px] py-[15px] font-bold;
  }
  .circle_third >*{
    @apply text-gray font-bold items-center text-center;
  }
  .input_time{
    @apply w-32 bg-gray-light pt-4 px-3 pb-4 text-body-1 font-bold text-black outline-none border-0 rounded-[10px];
  }
  .input_time::-webkit-inner-spin-button { 
    display: none;
  }
  .font-label{
    @apply flex justify-center items-center w-10 h-10 rounded-full;
  }
  .color-label{
    @apply flex justify-center items-center w-10 h-10 rounded-full;
  }
}